<template>
	<view class="page">
		<u-form :model="form" ref="uForm" :label-width="180">
			<view class="form-item-box">
				<u-form-item label="收货人" prop="receiverName" required>
					<u-input v-model.trim="form.receiverName" placeholder="请输入收货人" />
				</u-form-item>
				<u-form-item label="联系电话" prop="phone" required>
					<u-input v-model.trim="form.phone" placeholder="请输入联系电话" :maxlength="11" />
				</u-form-item>
				<u-form-item label="所在地址" required>
					<u-input v-model="form.location" placeholder="请选择所在地址" @click="showRegionPicker" />
				</u-form-item>
				<u-form-item label="详细地址" prop="detailAddress" required>
					<u-input type="textarea" v-model="form.detailAddress"  placeholder="请输入详细地址" autoHeight />
				</u-form-item>
			</view>
			
			<view class="form-item-box">
				<view class="default-address-box">
					<view class="">
						设为默认地址
					</view>
					<view class="">
						<u-switch v-model="isDefault" @change="switchChange"></u-switch>
					</view>
				</view>
			</view>
		</u-form>
		<view class="addSite" @click="save">
			提交保存
		</view>
		<u-picker mode="region" ref="uPicker" v-model="show" @confirm="confirm" />
	</view>
</template>

<script>
	// import {
	//     addressSave,
	// 	addressUpdate
	// } from "@/api/address.js";
export default {
	data() {
		return {
			show: false,
			isDefault: false,
			form: {
				receiverName: '',
				phone: '',
				location: '',
				detailAddress: '',
				isDefault: false,
				province: '',
				city: '',
				city: ''
			},
			rules: {
				receiverName: [
					{ 
						required: true, 
						message: '请输入收货人', 
						trigger: ['change','blur'],
					}
				],
				phone: [
					{
						required: true, 
						message: '请输入联系电话', 
						trigger: ['change','blur'],
					}
				],
				// location: [
				// 	{
				// 		required: true, 
				// 		message: '请选择所在地址', 
				// 		trigger: ['change','blur'],
				// 	}
				// ],
				detailAddress: [
					{
						required: true, 
						message: '请输入详细地址', 
						trigger: ['change','blur'],
					}
				],
			}
		};
	},
	onReady() {
		this.$refs.uForm.setRules(this.rules)
	},
	onLoad(params) {
		console.log(JSON.parse(JSON.stringify(params)), 'params===>')
		if (params.id) {
			this.form = params
			if (params.province) {
				this.form.location = params.province + ' ' + params.city + ' ' + params.area
			}
		}
		this.isDefault = params.isDefault == 1 ? true : false
		this.form.isDefault = params.isDefault == 1 ? true : false
	},
	methods: {
		showRegionPicker() {
			this.show = true;
		},
		confirm(e) {
			console.log(e, 'eee')
			this.form.location = e.province.label + ' ' + e.city.label + ' ' + e.area.label
			this.form.province = e.province.label
			this.form.city = e.city.label
			this.form.area = e.area.label
		},
		save() {
			this.$refs.uForm.validate(valid => {
				if (valid) {
					console.log('验证通过',this.form);
					if (this.form.receiverName.length > 40 || this.form.receiverName.length < 2) {
					    this.$u.toast('姓名输入格式有误，请输入2~40位汉字或字母');
					    return;
					}
					if (!this.$u.test.mobile(this.form.phone)) {
					    this.$u.toast('手机号码输入有误');
					    return;
					}
					if (!this.form.province || !this.form.city || !this.form.area) {
					    this.$u.toast('请选择省市区');
					    return;
					}
					this.form.isDefault = this.form.isDefault ? 1 : 0
					if (this.form.id) {
						// addressUpdate(this.form).then(res => {
						// 	if (res.code == 200) {
						// 		this.$u.toast('修改成功');
						// 		if (this.form.types = 'historyOrder') {
						// 			uni.navigateBack({
						// 				delta: 1
						// 			});
						// 		} else {
						// 			this.go({
						// 			    url: '/pages/address/address-list'
						// 			})
						// 		}
						// 	}
						// })
					} else {
						// addressSave(this.form).then(res => {
						// 	if (res.code == 200) {
						// 		this.$u.toast('新增成功');
						// 		if (this.form.types = 'historyOrder') {
						// 			uni.navigateBack({
						// 				delta: 1
						// 			});
						// 		} else {
						// 			this.go({
						// 			    url: '/pages/address/address-list'
						// 			})
						// 		}
						// 	}
						// })
					}
					
				} else {
					console.log('验证失败');
				}
			})
		},
		switchChange(e) {
			console.log(e, 'e')
			this.form.isDefault = e
			console.log(this.form.isDefault, 'this.form.isDefault')
		}
	}
};
</script>

<style lang="scss" scoped>
	.page {
		// padding: 20rpx;
		.form-item-box {
			background-color: #fff;
			padding: 20rpx 20rpx 20rpx 40rpx;
			// border-radius: 16rpx;
			.default-address-box {
				display: flex;
				justify-content: space-between;
				align-items: center;
			}
		}
		.form-item-box:nth-child(n+1) {
			margin-top: 20rpx;
		}
		
		.addSite {
			display: flex;
			justify-content: space-around;
			width: 600rpx;
			position: absolute;
			bottom: 60rpx;
			left: 80rpx;
			background-color: #285EE4;
			border-radius: 60rpx;
			font-size: 30rpx;
			color: #fff;
			padding: 15rpx 40rpx;
		}
	}
</style>
